
.App {
  text-align: center;
}

img{
  height: 50px;
  font-size: 2em;
  width: 1434px;
  height: 663px;
  opacity: 0.7;
}
nav{
  background:#5DADE2;
  padding: 10px;
  text-align: center;

} 

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

input {
  height: 50px;
  font-size: 2em;
  width: 200px;
}
.App-header {
   background-color: #1E90FF;
  margin: 0; 
  padding: 0;

}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
